body {
  margin: 0;
  padding: 0 0 15px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 320px;
  max-width: 800px;
  margin: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.cat-loader__img-wrapper {
  margin-bottom: 10px;
  overflow: hidden;
}

.cat-loader__img-wrapper img {
  width: 100%;
  -webkit-transition: ease-in-out 0.5s;
  transition: ease-in-out 0.5s;
}

.cat-loader__img-wrapper img:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.cat-loader__btn {
  width: 100%;
}

.spinner {
  width: 100%;
}

.cat-saver__form {
  margin: 15px 0;
  padding: 5px;
  border: 1px solid #e1e1e1;
  border-radius: 3px;
}

.cat-saver__form--label,
.cat-saver__form--btn {
  width: 100%;
}

.bordered {
  display: flex;
  border-bottom: 1px solid #efefef;
}
.bordered-body {
  flex: 1 1 auto;
  padding: 1.25rem;
}
.cat-list {
  margin: 25px 0 15px;
}

.cat-list__image-wrapper {
  margin: 10px 0;
  height: 200px;
  overflow: hidden;
}

.cat-list__image-wrapper--image {
  width: 100%;
  object-fit: contain;
}

